import './App.css';
import { useState } from 'react';

import { Dictionary } from './logic.js';
import { csvData } from './data.js';
import { $, jQuery } from 'jquery';
import * as csvParser from 'jquery-csv';

function App() {
    
    const [dict, setDict] = useState(null);
    if (dict == null) {
        console.log('init data');
        setDict(new Dictionary(csvParser.toObjects(csvData)));
    }
    const [results, setResults] = useState(null);

    function handler(query) {
        let results = search(query, dict);
        setResults(results);
    }

  return (
    <div className="App">
          <header>
          <div className="title justify-left">
                  <span className="title-thin">BASA</span><br/>
              <span className="title-thick">NUMO</span>
          </div>
              <SearchBar onQuery={handler} />
              <div className="title justify-right">
                  <span className="title-thin">TO</span><br/>
              <span className="title-thick">ENGLISH</span>
          </div>
        </header>
          <main>
              <Results data={ results } />
        </main>
          <footer>
              <a href="https://sites.google.com/view/basanumo/"
                  className="footer-link">
                  official site
              </a>
              <a href=""
                  className="footer-link">
                  github
              </a>
          </footer>
      </div>
  );
}

export default App;

function SearchBar({ onQuery }) {
    return (
        <form onSubmit={e => {
            e.preventDefault(); onQuery(e.target.elements.query.value);
        }}
            className="search-bar">
            <input
                type="text"
                id="query"
                placeholder="put a word here"
                autoComplete="off" autoCorrect="off" autoCapitalize="off" spellCheck="false"
                autoFocus
            ></input>
            <button
                type="submit"
                id="submit-button">
                <img src="search-icon.svg" />
                <span className="button-text">search</span>
            </button>
        </form>
    );
}

function Results({ data }) {

    console.log('update results: '+data);
    if (data == null)
        return <div className="empty-results">Try looking up an English or Numo word!</div>;
    if (data.length == 0)
        return <div className="empty-results">NOTHING'S FOUND</div>;
    let resultBoxes = [];
    for (let i = 0; i < data.length; i++) {
        let result = data[i];
        resultBoxes.push(<ResultBox entryArray={result} key={i} />);
    }
    return <div className="results">{resultBoxes}</div>;

}

function ResultBox({entryArray}) {
    let entryBoxes = [];
    let first = true;
    for (let i = 0; i < entryArray.length; i++) {
        let entry = entryArray[i];
        entryBoxes.push(<EntryBox entry={entry} first={first} key={i} />);
        first = false;
    }
    return <div className="result">{entryBoxes}</div>;

}


function EntryBox({ entry, first }) {
    if (first)
        return (
        <div className="entry">
            <div className="headword">{entry.Term}</div>
            <div className="word-class">{entry.Class}</div>
            <div className="translation">{entry.Translation}</div>
        </div>
        );
    else
        return (
        <div className="entry">
            <div className="headword">+ {entry.Term}</div>
            <div className="word-class">{entry.Class}</div>
            <div className="translation">{entry.Translation}</div>
        </div>
    );
}


function search(query, dictionary) {
    if (query == "")
        return null;
    let words = query.split(' ');
    let results = [];
    if (words.length == 1) {
        // analyze one word
        let resultsA = dictionary.analyze(query);
        results = resultsA;
        let lowercase = query.toLowerCase()
        if (lowercase != query) {
            let resultsB = dictionary.analyze(lowercase);
            results = results.concat(resultsB);
            query = lowercase; // it's easier to work with lower case
        }
        const regex_te = /te$/;
        if (regex_te.test(query)) {
            // remove the past tense suffix and analyze (accept only verbs)
            let stem = query.replace(regex_te, '');
            let resultsC = dictionary.analyze(stem);
            resultsC = resultsC.filter(analysis => analysis.length > 0 && analysis[analysis.length - 1].Class == 'Verb');
            results = results.concat(resultsC);
        }
    }
    if (words.length > 1 || results.length == 0) {
        results = results.concat(dictionary.lookUp(query));
        // look up phrases and individual words, also in the other language
        for (const word of words) {

            results = results.concat(dictionary.lookUp(word));
        }
        // filter out duplicates
        results = results.filter(function (item, pos) {
            return results.indexOf(item) == pos;
        });
    }
    return results;
}
